@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@200&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:"Exo 2",sans-serif;background-color:#1e1e1e;color:white;height:100vh;display:flex;justify-content:center;align-items:center}.fa-edit:hover{color:green}header{display:flex;align-items:center;margin-bottom:30px}header h1{font-size:2rem;color:#0081a1;font-family:"Exo 2",sans-serif;margin-left:20px}header img{width:60px;height:60px}div.addList{transition:all 0.5s 0s ease;position:relative;margin-left:70px;margin-bottom:30px;background-color:white}div.addList input{background:white;outline:none;border:none;padding:10px;border:1px solid white;font-size:1.1rem}div.addList .togIcon{transition:all 0.5s 0s ease;color:#0081a1;position:absolute;top:30%;left:88%;cursor:pointer}div.addList .fa-edit{color:green}div.showList{text-align:left;margin-left:70px}div.showList ul{list-style-type:none;margin-bottom:20px}div.showList li{display:flex;justify-content:space-between;align-items:center;font-family:"Exo 2",sans-serif;font-size:1rem;background-color:#0081a1;margin:20px 0px;padding:8px 15px;width:300px}div.showList li h3{font-family:Arial, Helvetica, sans-serif;font-weight:400;text-transform:capitalize;line-height:1.6rem}div.showList li div.icons{display:flex}div.showList li .edit{margin-right:12px}div.showList li .edit,div.showList li .delete{cursor:pointer}div.showList li:hover{background-color:white;color:black}div.showList li:hover .fa-edit{color:green}div.showList li:hover .fa-trash-alt{color:red}div.showList button{background-color:transparent;border:solid 1px #0081a1;color:#0081a1;cursor:pointer;padding:8px 15px;font-size:1.2rem}div.showList button:hover{background-color:#0081a1;color:white}@media screen and (max-width: 640px){header{flex-direction:column}header h1{margin-left:0px}header img{margin-bottom:20px}div.showList,div.addList{margin-left:0px}div.showList{text-align:center}h3{text-align:left}li.edit{margin-left:20px}}

